<template>
  <div>
    <div class="row">
        <div class="col-md-8">

          <b-alert show>Default Alert</b-alert>

            <table class="table table-striped mt-2">
                <thead class="thead-dark">
                <tr scope="row" class="table-dark">
                    <th scope="col" class="table-dark col-md-6">File Name</th>
                    <th scope="col" class="table-dark col-md-1"></th>
                    <th scope="col" class="table-dark col-md-1"></th>
                </tr>
                </thead>
                <tbody>
                  <tr v-for="dataSource in dataSources" :key="dataSource.id">
                    <td>{{dataSource.fileName}}</td>
                     <td><a href="#" class="btn btn-outline-dark btn-sm" role="button">View</a></td>
                     <td><a href="#" class="btn btn-outline-danger btn-sm" @click="showModal = true" data-toggle="modal" data-target="#confirm-delete">Delete</a></td>
                  </tr>  
                </tbody>
            </table>
        </div>
   
    </div>

    <div>
      <modal v-if="showModal" @close="showModal = false">
        <!--
          you can use custom content here to overwrite
          default content
        -->
        <h3 slot="header">Delete Data Source?</h3>
      </modal>
    </div>
  </div>
</template>

<script>
  import ConfirmModal from '../components/ConfirmModal.vue'
//
  export default {
    components: {
      modal: ConfirmModal
    },
    data () {
      return {
         showModal: false
      }
    },
    computed: {
      dataSources() {
        console.log(this.$store.getters.dataSources)
        return this.$store.getters. dataSources
      }
    },
    created: function () {
       this.$store.dispatch('fetchDataSources')
    },
  }
</script>


